.Activities {
  .hb-card {
    padding: 0 20px;
    margin-top: 44px;
    padding-top: 36px;
    padding-bottom: 12px;

    .hb-card-keyword.not-hover {
      background-color: var(--aquamarin-20);
    }

    .wrapper {
      margin: 0 auto 24px;
      border-radius: 16px;
      background-color: white;
      max-width: 746px;
      position: relative;

      .header {
        color: var(--nightblue-100);
        border-bottom: 1px solid var(--petrol-5);
      }

      .activity-remove {
        background-color: var(--petrol-10);
        color: white;
      }
    }

    .wrapper.dark {
      background-color: var(--petrol-100);

      .header {
        color: white;
        border-bottom: 1px solid var(--petrol-80);
      }

      .activity-remove {
        background-color: var(--petrol-50);

        .remove:before,
        .remove:after {
          background-color: var(--petrol-100);
        }
      }
    }

    .header {
      padding: 16px;

      .header-title {
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        letter-spacing: 0.005em;
      }

      .header-content {
        margin-top: 6px;
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;
        letter-spacing: 0.005em;
      }
    }

    .body {
      padding: 16px;
    }

    .link-item {
      border: 1px solid var(--petrol-5);
      border-radius: 32px;

      .item-title {
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        letter-spacing: 0.005em;
        color: var(--nightblue-100);
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }

    .activity-remove {
      width: 23px;
      height: 23px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      top: 16px;
      right: 16px;
      font-size: 18px;
      font-weight: 500;
      cursor: pointer;

      &:hover {
        opacity: 0.6;
      }

      .remove:before,
      .remove:after {
        position: absolute;
        left: 10px;
        top: 6px;
        content: ' ';
        height: 12px;
        width: 3px;
        background-color: white;
        border-radius: 16px;
      }

      .remove:before {
        transform: rotate(45deg);
      }

      .remove:after {
        transform: rotate(-45deg);
      }
    }
  }

  .activity-trainer {
    .avatar {
      width: 40px;
      height: 40px;
      border-radius: 50%;
    }

    .trainer-body {
      margin-left: 8px;
      color: var(--nightblue-100);

      .title {
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        letter-spacing: 0.005em;
      }

      .content {
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 0.005em;
        margin-top: 6px;
      }
    }
  }

  .activities-no-items {
    max-width: 746px;
    height: 363px;
    background: linear-gradient(90deg, #378a99, #98ced9);
    border-radius: 24px;
    padding: 20px 20px 0px;
    margin: 44px auto;
    position: relative;
    font-weight: 500;
    font-size: 19px;
    line-height: 25px;
    letter-spacing: -0.01em;
    color: white;

    img {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      margin-left: auto;
      margin-right: auto;
      width: 281px;
      height: 281px;
    }
  }

  .hb-link-item {
    background-color: white;
    padding: 15px;
    border-radius: 32px;
    height: 124px;
    display: flex;
    align-items: center;

    .cover-image {
      overflow: hidden;
      display: block;
      border-radius: 20px;
      width: 94px;
      height: 94px;
      background-size: cover;
    }

    .content-wrapper {
      padding: 8px 0px 8px 10px;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      flex: 1;
    }
  }
}
